body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.bG {

  background: rgb(2, 2, 2);
  background: radial-gradient(circle, rgba(255, 255, 255, 0.822) 30%, rgba(255, 188, 229, 0.356) 50%);  
  height: 100vh;
  overflow-y: auto;
}

.maxHeight {

  height: 85vh;
}

.maxWidth {

  width: 100%;
} 

.menuDashBg {
  
  clip-path: polygon(34% 3%, 53% 7%, 63% 13%, 68% 21%, 71% 33%, 66% 44%, 56% 55%, 38% 71%, 0 100%, 0 1%);
  background: rgb(252, 236, 243);
  background: linear-gradient(200deg, rgb(255, 228, 241) 30%, rgb(255, 196, 172) 80%); 
  overflow-y: auto;
  
}


code {
  font-family: source-code-pro, Menlo,  Monaco, Consolas, 'Courier New',
    monospace;
}

.text-link {
  color: inherit;
  text-decoration: inherit;
}

.text-link:hover{

  color: inherit;
  text-decoration: inherit;
}

.marginLeft {

  margin-left: 10px;
}

.marginAuto {

  margin: 0 auto !important;
}

.borderTest {

  border: 2px solid black;

}

.flex {

  display: flex;
  justify-content: center;
  align-items: center;
}

.textColor {

  color: #424242
}

.overFloxWrap {

  overflow-wrap: anywhere;
}